<template>
    <div id="main">
      <div id="mine">
        <div id="mine-header">
          <div>
            <img src="static/assets/user.png" alt="user">
          </div>
        </div>
        <div id="mine-function">
          <div id="favor" class="fun-item">
            <img src="static/assets/favor.png" alt="favor">
            <p>收藏</p>
          </div>
          <div id="advice" class="fun-item" @click="advice">
            <img src="static/assets/advice.png" alt="advice">
            <p>反馈</p>
          </div>
          <div id="manage" class="fun-item" @click="manage">
            <img src="static/assets/manage.png" alt="manage">
            <p>管理</p>
          </div>
          <div id="setting" class="fun-item">
            <img src="static/assets/setting.png" alt="setting">
            <p>设置</p>
          </div>
          <div></div>
        </div>
      </div>
    </div>
</template>

<script>
  export default {
    name: 'mine',
    methods: {
      advice: function () {
        this.$router.push({path: '/advice'})
      },
      manage: function () {
        this.$router.push({path: '/manage'})
      }
    },
    mounted () {
    }
  }
</script>

<style scoped>
  #mine{
    width: 100%;
    height: calc(100% - 140px);
    overflow: scroll;
  }
  #mine-header{
    width: 100%;
    height: 800px;
    background-color: #1296db;
  }
  #mine-header>div{
    width: 270px;
    height: 270px;
    overflow: hidden;
    background-color: white;
    border-radius: 135px;
    position: relative;
    left: calc((100% - 270px) / 2);
    top: calc((100% - 270px) / 2);
  }
  #mine-header>div img{
    width: 100%;
    height: 100%;
  }
  #mine-function{
    width: 100%;
    height: auto;
    margin: 20px 0;
  }
  .fun-item{
    width: 90%;
    height: 120px;
    overflow: hidden;
    margin: 0 5%;
  }
  .fun-item:not(:first-of-type){
    border-top: 3px solid #f0f2f5;
  }
  .fun-item img{
    float: left;
  }
  .fun-item p{
    font-size: 48px;
    line-height: 120px;
    float: left;
  }
  #favor img{
    width: 72px;
    height: 72px;
    margin: 24px 30px 24px 30px;
  }
  #advice img{
    width: 64px;
    height: 64px;
    margin: 28px 33px 28px 35px;
  }
  #manage img{
    width: 68px;
    height: 68px;
    margin: 27px 34px 25px 30px;
  }
  #setting img{
    width: 72px;
    height: 72px;
    margin: 25px 31px 23px 29px;
  }
</style>



